*,
*::before,
*::after {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  text-decoration: none;
  list-style: none;
}

html {
  margin: 0 !important;
  font-size: 62.5%;
}
@media only screen and (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}
@media only screen and (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}
@media only screen and (max-width: 37.5em) {
  html {
    font-size: 43.75%;
  }
}

body {
  box-sizing: border-box;
}

body {
  font-family: "Inter var" !important;
  color: #111111;
}

h1 {
  font-size: 4.5rem;
  text-transform: uppercase;
  letter-spacing: 3px;
}

h2 {
  font-size: 2.5rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-weight: 600;
}

h3 {
  font-size: 2.5rem;
  letter-spacing: 3px;
  font-weight: 500;
}

ul {
  margin: 0 !important;
}

p {
  margin: 0 !important;
}

.col {
  padding: 0 !important;
}

.btn {
  text-transform: uppercase;
  display: inline-flex !important;
  align-items: center !important;
  justify-content: center !important;
  color: #111111;
  font-size: 1.6rem;
  cursor: pointer;
  white-space: nowrap;
}
.btn--subscribe {
  background-color: #111111 !important;
  border: none !important;
  color: #fff !important;
  padding: 1.5rem 2rem !important;
  border-radius: 0.7rem !important;
  transition: all 0.3s !important;
  will-change: transform;
}
.btn--subscribe:hover {
  opacity: 0.8;
}
.btn--subscribe:active {
  transform: scale(0.97);
}
.btn--subscribe-2 {
  font-weight: 600 !important;
  color: #111111 !important;
  cursor: pointer;
  padding: 0 !important;
  background-color: transparent !important;
}

.fa-brands {
  font-size: 3rem;
  color: #111111;
}
.fa-brands:hover {
  opacity: 0.8;
}

.newsletter {
  background-color: #1e96ba;
}
.newsletter__left {
  margin-right: 10rem;
}
@media only screen and (max-width: 75em) {
  .newsletter__left {
    margin-right: 0;
    text-align: center;
    max-width: 49rem;
  }
}
.newsletter__box {
  display: grid;
  grid-template-columns: 2fr 1fr;
  align-items: center;
}
@media only screen and (max-width: 75em) {
  .newsletter__box {
    grid-template-columns: 1fr;
    justify-items: center;
  }
}
.newsletter__right {
  padding: 1rem 0;
  border-bottom: 2px solid #2c2c2c;
}
@media only screen and (max-width: 75em) {
  .newsletter__right {
    padding: 3rem 0 1rem 0;
    margin-top: 2rem;
    max-width: 59rem;
  }
}
@media only screen and (max-width: 56.25em) {
  .newsletter__right {
    max-width: 50rem;
  }
}
.newsletter-input {
  margin-right: 2rem;
  padding: 1.5rem 0 1.5rem 0;
  border: none;
  font-weight: 600;
  display: inline !important;
  width: 100%;
  background-color: transparent;
}
.newsletter-input:focus {
  outline: none;
}
.newsletter-input::placeholder {
  color: #111111;
  opacity: 1;
  font-size: 1.6rem !important;
  font-weight: 600;
}

header {
  height: 87vh;
}

.navigation {
  transition: all 0.3s;
  z-index: 5;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  border-bottom: 1px solid #eaeaea;
}

.nav-inner {
  height: 13vh;
}

.row {
  margin-top: 13vh !important;
  height: 87vh;
}

.search-icon {
  font-size: 1.8rem;
  cursor: pointer;
  display: inline-flex !important;
  align-items: center !important;
  justify-content: center !important;
  height: 100%;
  transition: all 0.3s;
}
.search-icon:active {
  transform: scale(0.9);
}

.overlay-box {
  width: 100%;
  display: block;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
  z-index: 99;
}

.overlay {
  display: block;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(17, 17, 17, 0.8);
  z-index: 98;
}

.search-bar {
  position: fixed;
  height: 115vh;
  transition: all 0.5s;
  top: 0;
  width: 40%;
  right: -40%;
  background-color: #fff;
  z-index: 100;
}

.cross-icon {
  position: absolute;
  cursor: pointer;
  top: 0%;
  right: 3%;
  font-size: 3.5rem;
}
.cross-icon:active {
  transform: scale(0.9);
}

.search-bar-input {
  padding: 1.5rem;
  margin-top: 7rem;
  width: 90%;
  font-size: 1.6rem;
  border: transparent;
}
.search-bar-input:focus {
  outline: none;
}

.search-bar-outter {
  margin: 0 3rem;
  padding: 0 1rem;
  border-bottom: 0.2rem solid #111111;
}

.hero-section {
  background-color: #fff;
}
.hero-section__right {
  height: 100%;
  display: flex;
  justify-content: center;
}
.hero-section__img {
  height: 100%;
}
.hero-section__left {
  position: relative;
}
.hero-section__text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.nav-links {
  height: 100%;
}

.nav-item {
  height: 100%;
  display: flex;
  align-items: center;
}

.nav-link {
  font-size: 1.6rem;
  font-weight: 300;
  color: #111111;
  text-transform: uppercase;
  height: 100%;
  display: flex;
  align-items: center;
  border-bottom: 2px solid transparent;
}
.nav-link:hover {
  border-bottom: 2px solid #1e96ba;
}

.dropdown {
  display: inline-block;
  transition: all 0.3s;
  display: flex;
  height: 100%;
  align-items: center;
}
.dropdown:hover .dropdown__content {
  display: block;
}
.dropdown__content {
  display: none;
  position: absolute;
  font-size: 1.5rem;
  font-weight: 300;
  border-top: 1px solid #eaeaea;
  top: 13vh;
  right: 0;
  background-color: #fff;
  width: 100%;
  padding: 12px 16px;
  z-index: 1;
  transition: all 3s;
  border-radius: 0.5rem;
}

.cat-item a {
  color: #111111;
  font-weight: 400;
}

.copyrights {
  font-size: 1.4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #1e96ba;
  color: #111111;
  font-weight: 600;
}